<script>
import Navbar from './Navbar'
import Sidebar from './Sidebar/Sidebar'

import $ from 'jquery'
$('.collapse').collapse('hide')

export default {
  components: {
    Navbar,
    Sidebar
  },
  data () {
    return {
      Title: {
        titleText: 'WELCOME TO<br><span>LING LING\'S</span> PARK',
        subText: '',
        bgColor: 'transparent'
      }
    }
  },
  methods: {
    toAboutMe () {
      this.$router.push('/about-me')
    }
  }
}
</script>

<style scope lang="scss">
</style>



