<template lang="pug">
nav(
  class="navbar sticky-top navbar-expand-lg"
  :class="{'homePage':this.$route.name === 'Home'}"
)
  .navbar-header
    // a(class="navbar-brand") 翎翎的家
  button#navBtn(
    class="navbar-toggler collapsed d-lg-none mr-5"
    type="button" data-target="#navbarContent" data-toggle="collapse"
  )
    // |☰
    span(class="lines line-1")
    span(class="lines line-2")
    span(class="lines line-3")

  #navbarContent(class="collapse navbar-collapse")
    ul(class="navbar-nav px-5 pb-2 p-lg-0 mr-lg-5 ml-auto" :style="bgNavbarContent()")
      li(class="nav-item")
        router-link(class="nav-link" to="/") 首頁
      li(class="nav-item")
        router-link(class="nav-link" to="/about-me") 關於我
      li(class="nav-item")
        router-link(class="nav-link" to="/animation") 小動畫
      li(class="nav-item dropdown dropdown-hover")
        a(class="nav-link" href="#" aria-haspopup="true" aria-expanded="false" id="navbarDropdown" data-toggle="dropdown") 網頁工具
        div(class="dropdown-menu dropdown-menu-right" :style="bgDropdownMenu()" aria-labelledby="navbarDropdown")
          router-link(class="dropdown-item" to="/webtools/color-guide") SASS色卡工具
          router-link(class="dropdown-item" to="/webtools/grid-system") 網格工具
</template>

<script>
export default {
  props: ['navItemColor'],
  data () {
    return {
      color: this.navItemColor,
      bgNavbarContent: this.getBgNavbarContent,
      bgDropdownMenu: this.getBgDropdownMenu
    }
  },
  watch: {
    'navItemColor' (newVal) {
      this.color = newVal
    }
  },
  methods: {
    getBgNavbarContent () {
      return this.$mq === 'lg' ? { backgroundColor: 'transparent' } : { backgroundColor: this.color }
    },
    getBgDropdownMenu () {
      return this.$mq === 'lg' ? { backgroundColor: 'white' } : { backgroundColor: 'transparent' }
    }
  },
  created () {
    this.bgNavbarContent = this.getBgNavbarContent
  }
}
</script>

<style scope lang="scss">
.dropdown > .dropdown-menu.fade {
  opacity: 1;
  pointer-events: auto;
}
</style>



