import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '@/components/Home'
import AboutMe from '@/components/AboutMe'
import WebTools from '@/components/WebTools'
import SassColorGuide from '@/components/Pages/WebTools/SassColorGuide'
import GridSystem from '@/components/Pages/WebTools/GridSystem'

import Animation from '@/components/Dashboard'
import gost from '@/components/Pages/Animation/gost'
import pot from '@/components/Pages/Animation/pot'
import hand from '@/components/Pages/Animation/hand'
import candle from '@/components/Pages/Animation/candle'
import pumpkin from '@/components/Pages/Animation/pumpkin'
import spider from '@/components/Pages/Animation/spider'

Vue.use(VueRouter)

export default new VueRouter({
  routes: [
    {
      name: 'Home',
      path: '/',
      component: Home
    },
    {
      name: 'AboutMe',
      path: '/about-me',
      component: AboutMe
    },
    {
      name: 'WebTools',
      path: '/webtools',
      component: WebTools,
      children: [
        {
          name: 'SassColorGuide',
          path: 'color-guide',
          component: SassColorGuide
        },
        {
          name: 'GridSystem',
          path: 'grid-system',
          component: GridSystem
        }
      ]
    },
    {
      name: 'Animation',
      path: '/animation',
      component: Animation
    },
    {
      name: 'gost',
      path: '/gost',
      component: gost
    },
    {
      name: 'pumpkin',
      path: '/pumpkin',
      component: pumpkin
    },
    {
      name: 'spider',
      path: '/spider',
      component: spider
    },
    {
      name: 'hand',
      path: '/hand',
      component: hand
    },
    {
      name: 'candle',
      path: '/candle',
      component: candle
    },
    {
      name: 'pot',
      path: '/pot',
      component: pot
    }
  ]
})



